  import React, { useState } from 'react'
  import { Document, Page, pdfjs} from 'react-pdf';
  import styled from 'styled-components'

  import { SizeMe } from 'react-sizeme'

  import GlobalWrapper from 'components/global-wrapper'
  import media from 'utils/media-queries'
  import Link from 'gatsby-link'


  import Header from 'components/article/header'
  import CoverImage from 'components/article/coverimage'
  import ContentWrapper from 'components/article/contentwrapper'
  import Footer from 'components/footer'
  import FullSizeImage from 'components/article/fullsizeimage'
  import LargeImage from 'components/article/largeimage'

  import CoverSrc from 'img/articles/academia/biomimicry/00_intro_image_studio_biomimicry.jpg'
  import ContentPDF from 'img/articles/academia/biomimicry/10_studio_biomimicry.pdf'

  pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

  const Section = styled.section`
    margin: 64px 0;
    ${media.sm`
      margin: 0;
    `}
  `

  const Content = styled.div`
    canvas {
      width: 100% !important;
      height: auto !important;
    }
    max-width: 100%;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    p + h3 {
      margin-top: 42px;
    }
    padding: 0 0 0 0;
    ${media.lg`
      padding-right: 0;
    `}
    ${media.xs`
    max-width: 700px;
    padding: 0 0 0 0;
    `}
  `

  const EmbedContainer = styled.div`
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    width: 100%;
    height: auto;
    margin-top: 32px;
    margin-bottom: 32px;
    @media (min-width: 1000px) {
      width: 1000px;
      height: 562px;
      padding-bottom: 0px;
      margin-left: auto;
      margin-right: auto;
    }
  `

  const Video = styled.iframe`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  `

  const ImageGrid = styled.div`
    background: #f2f2f2;
    max-width: 100%;
    padding: 40px;
    margin: 32px 0;
    text-align: center;
    ${media.lg`
      padding: 32px;
      margin: 0 0 24px 0;
    `}
    ${media.md`
      margin: 0;
      margin: 0 0 16px 0;
    `}
    ${media.sm`
      padding: 16px;
    `}
  `

  const Img = styled.img`
    max-width: calc(100% - 96px);
    max-height: 300px;
    margin: 8px;
    display: inline-block;
    ${media.xl`
      max-height: 240px;
    `}
    ${media.lg`
      max-height: 200px;
    `}
    ${media.md`
      max-height: 120px;
      margin: 4px;
    `}
    ${media.sm`
      max-height: 80px;
      margin: 2px;
    `}
  `

  const LifeTime = () => {
    const [numPages, setNumPages] = useState(null);
    return (
      <GlobalWrapper>
        <Header title="Biomimicry" lead="Transit camp" />
        <CoverImage src={CoverSrc} focusX={'20%'} focusY={'100%'} />
        <Section>
          <ContentWrapper>
            <h2>Biomimicry</h2>
            <p>
            <b>Project location // </b> Maharashtra, India <br/>
            <b>Project type // </b> Workshop camp <br/>
            </p>
            <p>
            This project goal is to explore the principles of form in nature through sketching and physical models. 
            In doing so, derive ideas and inspiration that can be translated into a built environment as biomimicry.
            Unlike the human way of creating things, in nature - form, material and structure exist in unison. 
            It is not only elegant but an ever growing form that responds and adapts to external shifts.
            </p>
            <p>
            The premise of exploration sets on <b>selection of one natural object and explores geometric aspects</b>. 
            I chose cabbage as an object of study to understand the strong relation of nature with form, material 
            and structure. “Geometry in natural form” was derived through observations, drawing, model making and 
            scientific study. Further intention is to translate the geometric principles into a built form that 
            spans a  liveable space.
            </p>
          </ContentWrapper>
        </Section>
        <Content>
        <SizeMe
          monitorHeight
          refreshRate={128}
          refreshMode={"debounce"}
          render={({ size }) => (
          <div>
            <Document 
              file={ContentPDF} 
              onLoadSuccess={({ numPages })=>setNumPages(numPages)}>
              {Array.apply(null, Array(numPages))
                  .map((x, i)=>i+1)
                  .map(page => <Page pageNumber={page} width={size.width} renderMode="png" scale={1.0}/>)}
            </Document>
          </div>
        )} />
        </Content>
        <Footer />
      </GlobalWrapper>
    )
  }

  export default LifeTime
